import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import {Preinscritdocs} from '../../interfaces/preinscritdocs';


@Injectable()
export class PreinscritdocService {
  
  private URLajout: string;
  result:any;
  URL = 'api/v1/preinscritdocs';
  
   constructor(private http: Http) { }


  

  getAll(): Observable<any> {
    return this.http.get(this.URL)
    .map((res)=> res.json())
  }

  get(id: string): Observable<any> {
    return this.http.get(this.URL + '/' + id);
  }

  add(preinscritdocs: Preinscritdocs): Observable<any> {
    return this.http.post(this.URL, preinscritdocs);
  }
}


