import { Component, OnInit} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {PlaceService } from '../home/place.service';
import {SpecialiteService } from '../home/specialite.service';
import {ResdocService } from '../home/resdoc.service';
import { Pays } from '../../interfaces/pays';
import { Spes } from '../../interfaces/spes';
import { Docs } from '../../interfaces/docs';
import {ServiceService} from '../../service.service'
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {
term1:any;
term:any;
  stateForm: FormGroup;
  showDropDown = false;
  showDropDown1 = false;
  selected:string="";
  s: Pays[] = [];
  h: Spes[]=[];
  d:Docs[]=[];

  constructor(private serviceService:ServiceService,private router: Router,private resdocService:ResdocService ,private fb: FormBuilder,private placeService : PlaceService ,private specialiteService:SpecialiteService  ) {
    this.initForm()   
  }
 
  initForm(): FormGroup {
    return this.stateForm = this.fb.group({
      search: [null],
      searchk: [null]
    })}
  ngOnInit() {
    this.placeService.getAll()
      .subscribe(result => {
        console.log(result);
        this.s = result;
      }, failure => {
        console.error(failure);
      });

      this.specialiteService.getAll()
      .subscribe(result => {
        console.log(result);
        this.h = result;
      }, failure => {
        console.error(failure);
      });
      }

  closeDropDown() {
    this.showDropDown = !this.showDropDown;
  }
  closeDropDown1() {
    this.showDropDown1 = !this.showDropDown1;
  }
  selectValue(value) {
    this.stateForm.patchValue({"search": value});
    this.showDropDown1 = false;
    this.showDropDown = false;
  }
  selectedValue(value) {
    this.stateForm.patchValue({"searchk": value});
    
    this.showDropDown = false;
  }
  showSelected(s)
  {
    this.selected = s;

  }
  openDropDown() {
    this.showDropDown1 = false;
  }

  showVar: boolean = true;
    rechercherr(Typesp:string,Place:string){
     
      this.resdocService.rechercher(Typesp,Place)
      
       .subscribe(res =>{this.d=res
       
         (res==="Unable to find doc")
          alert (res)
  
});
}

  



}




