import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import {PreinscritdocService } from '../inscription/preinscritdoc.service';
import {Preinscritdocs} from '../../interfaces/preinscritdocs';
import { Pays } from '../../interfaces/pays';
import { Spes } from '../../interfaces/spes';
import {PlaceService } from '../home/place.service';
import {SpecialiteService } from '../home/specialite.service';
@Component({
  selector: 'app-inscription',
  templateUrl: './inscription.component.html',
  styleUrls: ['./inscription.component.css']
})
export class InscriptionComponent implements OnInit {
  
  newdoc: Preinscritdocs = {
    nomfam: '',
    tunisie:'',
    adresse:'',
    gov:'',
    spesialiste:'',
    tele: '',
    E_mail: '',
    commente: '',
  };
  stateForm: FormGroup;
  showDropDown = false;
  showDropDown1 = false;
  selected:string="";
   p:Preinscritdocs[]=[];
   s: Pays[] = [];
   h: Spes[]=[];
   term1:any;
   term:any;
  constructor(private fb: FormBuilder,private router: Router,private specialiteService:SpecialiteService ,private placeService:PlaceService,private preinscritdocService:PreinscritdocService) {
    this.initForm()   ;
   }
   initForm(): FormGroup {
    return this.stateForm = this.fb.group({
      search: [null],
      searchk: [null]
    })}
  ngOnInit() {
    this.preinscritdocService.getAll()
    .subscribe(result => {
      console.log(result);
      this.s = result;
    }, failure => {
      console.error(failure);
    });
    this.placeService.getAll()
    .subscribe(result => {
      console.log(result);
      this.s = result;
    }, failure => {
      console.error(failure);
    });

    this.specialiteService.getAll()
    .subscribe(result => {
      console.log(result);
      this.h = result;
    }, failure => {
      console.error(failure);
    });
  }
  closeDropDown() {
    this.showDropDown = !this.showDropDown;
  }
  closeDropDown1() {
    this.showDropDown1 = !this.showDropDown1;
  }
  selectValue(value) {
    this.stateForm.patchValue({"search": value});
    this.showDropDown1 = false;
    this.showDropDown = false;
  }
  selectedValue(value) {
    this.stateForm.patchValue({"searchk": value});
    
    this.showDropDown = false;
  }
  showSelected(s)
  {
    this.selected = s;

  }
  openDropDown() {
    this.showDropDown1 = false;
  }

  onSubmit() {
    this.preinscritdocService.add(this.newdoc)
    .subscribe(result => {
     
      console.log(result);
      this.router.navigate(['merci']);
      this.newdoc = {
        nomfam: '',
        tunisie:'',
        adresse:'',
        gov:'',
        spesialiste:'',
        tele: '',
        E_mail: '',
        commente: '',
      };
    }, err => {
      console.error(err);
      window.alert('An error occured while saving docteur');
    });
  }
  onReset () {
    this.newdoc = {
      nomfam: '',
      tunisie:'',
      adresse:'',
      gov:'',
      spesialiste:'',
      tele: '',
      E_mail: '',
      commente: '',
    };
  }
}




