import { BrowserModule } from '@angular/platform-browser';
import { CanActivate, CanActivateChild } from '@angular/router';
import { NgModule } from '@angular/core';
import {ServiceService} from './service.service'
import {UserService} from './user.service'
import {NgxPaginationModule} from 'ngx-pagination'; 
import { AppComponent } from './app.component';
import { HeaderComponent } from './components/header/header.component';
import { HomeComponent } from './components/home/home.component';
import {RouterModule} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { InscriptionComponent } from './components/inscription/inscription.component';
import { ContactComponent } from './components/contact/contact.component';
import { FooterComponent } from './components/footer/footer.component';
import { EmailComponent } from './components/email/email.component';
import {SpecialiteService } from './components/home/specialite.service';
import {PlaceService } from './components/home/place.service';
import {ResdocService} from './components/home/resdoc.service';
import {LoginService} from './components/login/login.service';
import {PreinscritdocService } from './components/inscription/preinscritdoc.service';
import { DropdownDirective } from './components/home/dropdown.directive';
import { FilterPipe } from './components/home/filter.pipe';
import {DocresComponent} from './components/docres/docres.component';
import { ThankyouComponent } from './components/thankyou/thankyou.component';
import { AdminComponent } from './components/admin/admin.component';
import {MailService} from './components/footer/mail.service';
import { DocComponent } from './components/doc/doc.component'
import { DoccComponent } from './components/docc/docc.component'
import { NgbCollapseModule } from '@ng-bootstrap/ng-bootstrap';
import {AuthGuard} from './auth-guard.service'
import { LoginComponent } from './components/login/login.component';
import { ClientComponent } from './components/client/client.component';

const routes = [{
  path: '', component: HomeComponent,pathMatch: 'full'
  }, {
  path: 'Inscription', component: InscriptionComponent
  },
   {
  path: 'Contact', component: ContactComponent
   }, 
   {
  path: 'email', component: EmailComponent
  },
  {
  path: 'resultat', component: DocresComponent
  },
 {
 path: 'merci', component: ThankyouComponent
},
{
  path: 'client', component: ClientComponent
 },
{ 
path: 'login', component: LoginComponent
},
{
path: 'admin', component: AdminComponent, canActivate: [AuthGuard]
 },
{
path: 'llll', component: DocComponent, canActivate: [AuthGuard] 
},
{ path: 'mmmm', component: DoccComponent, canActivate: [AuthGuard] 
},
{ path: '**', redirectTo: '/home' }
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HomeComponent,
    InscriptionComponent,
    ContactComponent,
    FooterComponent,
    EmailComponent,
    DocresComponent,
    // DropdownDirective,
    FilterPipe,
    ThankyouComponent,
    AdminComponent,
    DropdownDirective,
    DocComponent,
    DoccComponent,
    LoginComponent,
    ClientComponent,
  ],
  imports: [
    NgbCollapseModule,
    NgxPaginationModule,
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(routes),
    FormsModule,
    ReactiveFormsModule,
    HttpModule,    
  
  ],
  providers: [UserService,AuthGuard,LoginService,MailService,ServiceService,PreinscritdocService,PlaceService,SpecialiteService,ResdocService],
  bootstrap: [AppComponent]
})
export class AppModule { }



