import { Component, OnInit } from '@angular/core';
import { Docs } from '../../interfaces/docs';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {ResdocService } from '../home/resdoc.service';
import {PreinscritdocService } from '../inscription/preinscritdoc.service';
import {Preinscritdocs} from '../../interfaces/preinscritdocs';
import { Pays } from '../../interfaces/pays';
import { Spes } from '../../interfaces/spes';
import {PlaceService } from '../home/place.service';
import {SpecialiteService } from '../home/specialite.service';

@Component({
  selector: 'app-doc',
  templateUrl: './doc.component.html',
  styleUrls: ['./doc.component.css']
})
export class DocComponent implements OnInit {
  doctor: FormGroup = new FormGroup({
    // _id: new FormControl(''),
    Nom_prenom: new FormControl('',[Validators.required]),
    adresse:new FormControl('',[Validators.required]),
    tele:new FormControl(''),
    E_mail:new FormControl(''),
    Place:new FormControl(''),
    spes:new FormControl(''),
  });
  governorat: FormGroup = new FormGroup({
    Place: new FormControl('',[Validators.required]),
   
  });
  spesi: FormGroup = new FormGroup({
    Typesp: new FormControl('',[Validators.required]),
   
  });
  public isCollapsed = true;
  showDropDown = false;
  showDropDown1 = false;
  selected:string="";
   p:Preinscritdocs[]=[];
   pays: Pays[] = [];
   spes: Spes[]=[];
   d:Docs[]=[];
   term1:any;
   term:any;
   term3:any;
   showHead: boolean = true;
  constructor(private route: ActivatedRoute,private specialiteService:SpecialiteService ,private placeService:PlaceService,private preinscritdocService:PreinscritdocService,private fb: FormBuilder,private router: Router,private resdocService:ResdocService) {
    
   }
  
  ngOnInit() {
    this.resdocService.getAll()
    .subscribe(result => {
      console.log(result);
      this.d = result;
    }, failure => {
      console.error(failure);
    });
    this.preinscritdocService.getAll()
    .subscribe(result => {
      console.log(result);
      this.p= result;
    }, failure => {
      console.error(failure);
    });
    this.placeService.getAll()
    .subscribe(result => {
      console.log(result);
      this.pays = result;
    }, failure => {
      console.error(failure);
    });

    this.specialiteService.getAll()
    .subscribe(result => {
      console.log(result);
      this.spes = result;
    }, failure => {
      console.error(failure);
    });
  }

 
  onSubmit() {
    if (this.doctor.status === 'VALID') {
      this.resdocService.add({
        ...this.doctor.value
      }).subscribe(result => {
        console.log(result);
       
      }, err => {
        console.error(err);
      });
    } else {
      alert('Invalid data, please check validation errors');
    }
  }
  onReset () {
    this.doctor.reset() 
  } 
  onReset1 () {
    this.governorat.reset() 
  }
  onReset2 () {
    this.spesi.reset() 
  }
  onModifier() {
    if (this.doctor.status === 'VALID') {
      this.resdocService.update({
        ...this.doctor.value,
        doctor: this.doctor.get('d').value.map(d=> d._id) || []
      }).subscribe(result => {
        console.log(result);
        alert('Doctor updated successfully');
      }, err => {
        console.error(err);
      });
    } else {
      alert('Invalid data, please check validation errors');
    }
  }
  closeDropDown() {
    this.showDropDown = !this.showDropDown;
  }
  closeDropDown1() {
    this.showDropDown1 = !this.showDropDown1;
  }
  selectValue(value) {
    this.doctor.patchValue({"spes": value});
    this.showDropDown1 = false;
    this.showDropDown = false;
  }
  selectedValue(value) {
    this.doctor.patchValue({"Place": value});
    
    this.showDropDown = false;
  }
  showSelected(pays)
  {
    this.selected = pays;

  }
  openDropDown() {
    this.showDropDown1 = false;
  }
  onSubmit1() {
    if (this.governorat.status === 'VALID') {
      this.placeService.add({
        ...this.governorat.value
      }).subscribe(result => {
        console.log(result);
       
      }, err => {
        console.error(err);
      });
    } else {
      alert('Invalid data, please check validation errors');
    }
  }
  onSubmit2() {
    if (this.spesi.status === 'VALID') {
      this.specialiteService.add({
        ...this. spesi.value
      }).subscribe(result => {
        console.log(result);
       
      }, err => {
        console.error(err);
      });
    } else {
      alert('Invalid data, please check validation errors');
    }
  }
}




