import { Component, OnInit,ViewChild,AfterViewInit} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {LoginService } from '../login/login.service';
import { Logs } from '../../interfaces/logs';
import {UserService} from '../../user.service'
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  log:Logs[]=[];

  constructor(private loginService:LoginService,private router: Router,private user:UserService) { }

  ngOnInit() {
  }
  login1(username:string,pass:string){
    
     this.loginService.login(username,pass)
     
      .subscribe(res =>{
        if  (res==="non")
        {alert ('Le mot de passe ou pseudo est incorrect!')}
       else 
        this.user.setUserLoggedIn();
        this.router.navigate(['admin']);
     })
     }
  }


