import { Component,  Input,OnInit ,Output,EventEmitter} from '@angular/core';
import {MailService } from '../footer/mail.service';
import { Mailings } from '../../interfaces/mailings';
import {UserService} from '../../user.service'
@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  public isCollapsed = true;
  m:Mailings[]= [];
  @Input() mm;
  @Output() delete: EventEmitter<String> = new EventEmitter();
  showHead: boolean = true;
  newmail: Mailings = {
    name: '',
    email:'',
    sujet:'',
  };

_id:any;

  constructor(private user:UserService,private mailService:MailService) { }

  ngOnInit() {
    this.mailService.getAll()
    .subscribe(result => {
      console.log(result);
      this.m= result;
    }, failure => {
      console.error(failure);
    });
  }

  
  
onDelete () {
    if (window.confirm('Are you sure?')) {
      this.delete.emit(this.mm._id);
      
        // window.alert('An error occured while deleting email');
      }
  }
  supprimer(e) {
    console.log(e);
    this.mailService.delete(e)
    .subscribe(res => {
      this.m= this.m.filter(m => m._id !== e);
      alert('E_mail deleted successfully');
    }, err => {
      console.error(err);
    });
  }
}



