import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { Docs } from '../../interfaces/docs';
import 'rxjs/add/operator/map';


@Injectable()
export class ResdocService {
  
  private URLajout: string;
  result:any;
  URL = 'api/v1/docs';
  
   constructor(private http: Http) { }


  

  getAll(): Observable<any> {
    return this.http.get(this.URL)
    .map((res)=> res.json())
  }

  rechercher( Typesp:String,Place:String  ) {
    
         
            this.URLajout="api/v1/docs"
                +Typesp+"/"+Place;
              console.log(this.URLajout);
              return this.http.get(this.URLajout)
             .map(res => res.json())
            
              
          
     }
     rechercherville( Typesp:String,ville:String  ) {
      
           
              this.URLajout="api/v1/docss/"
                  +Typesp+"/"+ville;
                console.log(this.URLajout);
                return this.http.get(this.URLajout)
               .map(res => res.json())
              
                
            
       }
     add(doctor : Docs ): Observable<any> {
      return this.http.post(this.URL, doctor);
    }
    update(docs : Docs ): Observable<any> {
      return this.http.put(this.URL + '/' + docs._id, docs);
    }
    get(id: string): Observable<any> {
      return this.http.get(this.URL + '/' + id);
    }
}



